'use client'

import { Button } from '@mantine/core';

interface Task {
  date: string;
  startTime: string;
  endTime: string;
  description: string;
}

interface Props {
  tasks: Task[];
}

export default function DownloadBtn({ tasks }: Props) {
  const downloadCSV = () => {
    if (tasks.length === 0) return;

    const headers = ['Datum', 'Startzeit', 'Endzeit', 'Beschreibung'];
    const csvContent =
      [headers.join(','), ...tasks.map(t => [t.date, t.startTime, t.endTime, `"${t.description}"`].join(','))].join('\n');

    const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
    const url = URL.createObjectURL(blob);

    const link = document.createElement('a');
    link.href = url;
    link.setAttribute('download', `Wochenbericht_${new Date().toLocaleDateString('de-DE')}.csv`);
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  return (
    <div className="flex justify-center">
      <Button onClick={downloadCSV} className="py-3 px-8 bg-[#228BE6] hover:bg-[#1a6fb3] rounded-lg">
        Als CSV Herunterladen
      </Button>
    </div>
  );
}
