'use client'

import { useState } from 'react';
import { Textarea, Button } from '@mantine/core';
import { DatePicker, TimeInput } from '@mantine/dates';
import 'dayjs/locale/de';

interface Task {
  date: string;
  startTime: string;
  endTime: string;
  description: string;
}

interface Props {
  tasks: Task[];
  setTasks: React.Dispatch<React.SetStateAction<Task[]>>;
}

export default function Card({ tasks, setTasks }: Props) {
  const [date, setDate] = useState<Date | null>(null);
  const [startTime, setStartTime] = useState<Date | null>(null);
  const [endTime, setEndTime] = useState<Date | null>(null);
  const [description, setDescription] = useState('');

  const addTask = () => {
    if (!date || !startTime || !endTime || !description) return;
    const newTask: Task = {
      date: date.toLocaleDateString('de-DE'),
      startTime: startTime.toLocaleTimeString('de-DE', { hour: '2-digit', minute: '2-digit' }),
      endTime: endTime.toLocaleTimeString('de-DE', { hour: '2-digit', minute: '2-digit' }),
      description,
    };
    setTasks([...tasks, newTask]);
    setDate(null);
    setStartTime(null);
    setEndTime(null);
    setDescription('');
  };

  return (
    <section className="flex flex-col gap-4 mb-10">
      <DatePicker
        locale="de"
        placeholder="Datum auswählen"
        value={date}
        onChange={setDate}
        styles={{
          input: { backgroundColor: '#2D2D2D', color: 'white', border: '1px solid #404040' },
        }}
      />
      <div className="flex gap-2">
        <TimeInput
          placeholder="Startzeit"
          value={startTime}
          onChange={setStartTime}
          styles={{ input: { backgroundColor: '#2D2D2D', color: 'white', border: '1px solid #404040' } }}
        />
        <TimeInput
          placeholder="Endzeit"
          value={endTime}
          onChange={setEndTime}
          styles={{ input: { backgroundColor: '#2D2D2D', color: 'white', border: '1px solid #404040' } }}
        />
      </div>
      <Textarea
        placeholder="Tagesbericht"
        autosize
        minRows={4}
        value={description}
        onChange={(e) => setDescription(e.currentTarget.value)}
        styles={{
          input: { backgroundColor: '#2D2D2D', color: 'white', border: '1px solid #404040' },
          label: { color: 'white' },
        }}
      />
      <Button onClick={addTask} className="bg-[#228BE6] hover:bg-[#1a6fb3]">
        Hinzufügen
      </Button>
    </section>
  );
}
