'use client'

import { useState } from 'react';
import Card from './components/Card';
import DownloadBtn from './components/DownloadBtn';

export default function HomePage() {
  const [tasks, setTasks] = useState<{ date: string; startTime: string; endTime: string; description: string }[]>([]);

  return (
    <main className="flex justify-center py-10 px-4 lg:px-0">
      <section className="w-full max-w-[1350px] text-white flex flex-col gap-6">
        <Card tasks={tasks} setTasks={setTasks} />
        <DownloadBtn tasks={tasks} />
      </section>
    </main>
  );
}
